@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Qahiri&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span,
h1, h2, h3, h4, h5, h6, p, footer {
	margin: 0;
	padding: 0;
	vertical-align: baseline;
}

body {
    background: #4a90e2;
    font-family: Ubuntu;
}

.txtform {
    background: none repeat scroll 0 0 rgba(143, 32, 32, 0.07);
    background-color: white;
    border-color: -moz-use-text-color #9c1e1e #681818 -moz-use-text-color !important;
    border-radius: 6px 6px 6px 6px;
    border-style: none solid solid none;
    box-shadow: 8px 8px 6px rgba(0, 0, 0, 0.12);
    color: #9b8e8e !important;
    font-size: 1rem;
    line-height: 1.4em;
    padding: 20px 20px;
    resize: none;
}

.resultline {
    font-size: 1.5rem;
    font-weight: 300;
    letter-spacing: 4px;
}

.results {
    color: crimson;
    font-family: Qahiri;
    font-size: 2.5rem;
}

.foot {
    position: absolute;
    bottom: 0;
    height: 30px;
    width: 100%;
}
